.container {
    padding-top: 30px;
    width: 300px;
    margin: auto;
}

.input {
    padding: 5px 15px;
    width: 300px;
}

.user-list {
    margin-top: 30px;
}

.user {
    list-style: none;
    margin: 10px 0;
    padding: 10px;
    background: #eee;
    display: flex;
    justify-content: space-between;
}

.user-name {
    color: red;
    margin-right: 20px;
}

.user-userName {
    color: blue;
}

.user-email {
    margin-left: 20px;
}